(() => {
  const refs = {
    // Додати атрибут data-modal-open на кнопку відкриття
    openModalBtn: document.querySelector('[data-menu-open]'),
    // Додати атрибут data-modal-close на кнопку закриття
    closeModalBtn: document.querySelector('[data-menu-close]'),
    // Додати атрибут data-modal на бекдроп модалки
    modal: document.querySelector('[data-menu]'),

    closeAboutLink: document.querySelector('[data-menu-about-close]'),
    closeCatalogLink: document.querySelector('[data-menu-catalog-close]'),
    closeSaleLink: document.querySelector('[data-menu-sale-close]'),
    closeReviewsLink: document.querySelector('[data-menu-reviews-close]'),
    closeContactUsLink: document.querySelector('[data-menu-contact-us-close]'),
    
  };

  refs.openModalBtn.addEventListener('click', toggleModal);
  refs.closeModalBtn.addEventListener('click', toggleModal);
  refs.closeAboutLink.addEventListener('click', toggleModal);
  refs.closeCatalogLink.addEventListener('click', toggleModal);
  refs.closeSaleLink.addEventListener('click', toggleModal);
  refs.closeReviewsLink.addEventListener('click', toggleModal);
  refs.closeContactUsLink.addEventListener('click', toggleModal);

  
  function toggleModal() {
    // is-open це клас який буде додаватися/забиратися на бекдроп при натисканні на кнопки
    refs.modal.classList.toggle('is-open');
  }
})();
